		function preventDefaultForEnter(evt) {
			ctrlPressed = false;
			if (evt.keyCode === 13) {
				if (evt.preventDefault) {
					evt.preventDefault();
				} else {
					evt.returnValue = false;
				}
			}
			if (evt.keyCode == 65 && evt.ctrlKey) {
				ctrlPressed = true;
				evt.target.select();
				return;
			}
			if (evt.ctrlKey && (evt.keyCode == 67 || evt.keyCode == 88)) {
				ctrlPressed = true;
				return;
			}
		}

